.App {
  background: rgb(40, 44, 52);
  background: radial-gradient(
    circle,
    rgba(40, 44, 52, 0.4458158263305322) 0%,
    rgba(17, 17, 107, 0.8575805322128851) 47%
  );
}

.container2 {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100vw;
  border: 5px solid green;
}

.login-button {
  margin: 5% 35%;
}

.modal-header {
  background: rgb(4, 32, 153);
  background: radial-gradient(
    circle,
    rgba(4, 32, 153, 0.6615021008403361) 46%,
    rgba(40, 180, 224, 0.4430147058823529) 55%,
    rgba(2, 49, 101, 0.804359243697479) 84%
  );
}

.Login-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.Login-form {
  width: 420px;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: aliceblue;
}

.Login-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Login-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}
